const PangeaMTLinks = () => (
  <Helmet>
    <title>{config[config.default].title}</title>

    <link rel="apple-touch-icon" sizes="180x180" href="/favicons/pgweb/apple-touch-icon.png" />
    <link rel="icon" type="image/png" sizes="32x32" href="/favicons/pgweb/favicon-32x32.png" />
    <link rel="icon" type="image/png" sizes="16x16" href="/favicons/pgweb/favicon-16x16.png" />
    <link rel="manifest" href="/favicons/pgweb/site.webmanifest" />
    <link rel="mask-icon" href="/favicons/pgweb/safari-pinned-tab.svg" color="#5bbad5" />
    <meta name="msapplication-TileColor" content="#da532c" />
    <meta name="theme-color" content="#ffffff" />
  </Helmet>
);

export const HeadCmp = () => {
  return (
    <div>
      {config.default === 'autheticity' && <AutheticityLinks />}
      {config.default === 'pangeanic' && <PangeaMTLinks />}
    </div>
  );
};
