/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { injectIntl } from 'react-intl';
import { bindActionCreators } from 'redux';
import { Layout, Dropdown, Icon, Button, Menu, Spin, Row, Col } from 'antd';
import { Link, withRouter } from 'react-router-dom';

import axios from 'axios';

import { Logo, LayoutWrapper, DivHeader } from './styles';

import auth from './../../utils/auth';
import config from './../../config';

import { CallOut } from './../../components/CallOuts';
import { Routes, Menus } from './routes';

import { changeLang } from './../../reducers/auth';
import { getUser, enginesByUserv1 } from './../../services';
import * as notificationActions from './../../reducers/notification';
import * as processActions from './../../reducers/process';
import * as authActions from './../../reducers/auth';

import { HeadCmp } from 'components/HeadCmp';
import './index.css';

const { Header, Content, Footer, Sider } = Layout;

const jsUcfirst = string => {
  return string.charAt(0).toUpperCase() + string.slice(1);
};

const Dashboard = props => {
  const {
    location: { pathname },
    authState,
    intl: { messages },
  } = props;

  const [allowedFiles, setAllowedFiles] = useState('');
  const [collapsed, setCollapsed] = useState(false);
  const [loading, setLoading] = useState(false);
  const [defaultValue, setDefaultValue] = useState(0);

  useEffect(() => {
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();

    _getUser(source);
    _getEngines(source);
  }, []);

  const _getUser = async signal => {
    const {
      history,
      authAct: { fetchUser },
    } = props;

    setLoading(true);
    try {
      let result = await getUser(signal);
      fetchUser(result.data.user);
      setLoading(false);
    } catch (err) {
      auth.clearAppStorage();
      history.push('/');
      setLoading(false);
    }
  };

  const _getEngines = signal => {
    const {
      processAct: { getEngines, fetchRequest, fetchResponse },
    } = props;

    fetchRequest();
    enginesByUserv1(signal)
      .then(response => {
        let data = response.data;
        if (data.process && data.allowedFiles) {
          getEngines(data.process, data.process[0].name);
          setDefaultValue(jsUcfirst('translation'));
          setAllowedFiles(data.allowedFiles);
          fetchResponse();
        }
      })
      .catch(error => {
        fetchResponse();
      });
  };

  const onCollapse = () => {
    setCollapsed(!collapsed);
  };

  const saveCollapse = value => {
    setCollapsed(value);
  };

  const logout = () => {
    const { history } = props;
    auth.clearAppStorage();
    history.push('/');
  };

  const _selectProcess = type => {
    const {
      processAct: { selectProcess },
    } = props;
    selectProcess(type);
  };

  const _changeFileID = fileId => {
    const {
      processAct: { changeFileID },
    } = props;
    changeFileID(fileId);
  };

  const menu = (
    <Menu>
      <Menu.Item key="2">
        <Link to="/dashboard/profile">
          <Icon type="profile" /> &nbsp;&nbsp;{messages.menus.profile}
        </Link>
      </Menu.Item>
      <Menu.Item key="3" onClick={logout}>
        <Icon type="logout" /> {messages.menus.logout}
      </Menu.Item>
    </Menu>
  );

  const menuLang = (
    <Menu>
      <Menu.Item
        key="en"
        disabled={props.lang === 'en'}
        onClick={() => {
          props.authChangeLang('en');
        }}
      >
        <span role="img" aria-label="English">
          ðŸ‡ºðŸ‡¸
        </span>
        {messages.languages.en}
      </Menu.Item>
      <Menu.Item
        key="es"
        disabled={props.lang === 'es'}
        onClick={() => {
          props.authChangeLang('es');
        }}
      >
        <span role="img" aria-label="Spanish">
          ðŸ‡ªðŸ‡¸
        </span>
        {messages.languages.es}
      </Menu.Item>
    </Menu>
  );

  if (loading) {
    return (
      <Row justify="center" type="flex">
        <HeadCmp />
        <Col
          xs={12}
          style={{
            display: 'flex',
            justifyContent: 'center',
            marginTop: 60,
          }}
        >
          <Spin size="large" spinning={loading} tip={messages.default.loading + '...'} />
        </Col>
      </Row>
    );
  } else {
    return (
      <LayoutWrapper>
        <HeadCmp />
        <Sider width={256} collapsed={collapsed}>
          <Logo>
            {collapsed ? (
              <img alt="logo" src={config[config.default].logoMin} />
            ) : (
              <img alt="logo" src={config[config.default].logo} />
            )}
          </Logo>
          <Menus pathname={pathname} rol={authState.user.rol} user={authState.user} />
        </Sider>
        <Layout>
          <Header
            style={{
              background: '#fff',
              padding: 0,
            }}
          >
            <Icon
              className="trigger"
              type={collapsed ? 'menu-unfold' : 'menu-fold'}
              onClick={onCollapse}
            />
            <DivHeader>
              <Dropdown overlay={menuLang} placement="bottomRight">
                <Button style={{ marginLeft: 10 }} type="primary" shape="circle" icon="global" />
              </Dropdown>
              <Dropdown overlay={menu} placement="bottomRight">
                {/* <AvatarWrapper type="primary" style={{ marginLeft: 10 }} icon="user" /> */}
                <Button style={{ marginLeft: 10 }} type="primary" icon="user">
                  {authState.user && authState.user.fullName ? authState.user.fullName : ''}
                  {}
                </Button>
              </Dropdown>{' '}
              {authState.user.rol === 'client' && <CallOut text={authState} />}
            </DivHeader>
          </Header>{' '}
          <Content
            style={{
              margin: '40px 16px',
            }}
          >
            <Routes
              authState={authState}
              messages={messages}
              _changeFileID={_changeFileID}
              allowedFiles={allowedFiles}
              defaultValue={defaultValue}
              _selectProcess={_selectProcess}
              setCollapsed={saveCollapse}
            />
          </Content>
          <Footer
            style={{
              textAlign: 'center',
            }}
          >
            {config[config.default].footer}
          </Footer>
        </Layout>
      </LayoutWrapper>
    );
  }
};

function mapDispatchToProps(dispatch) {
  return {
    processAct: bindActionCreators(processActions, dispatch),
    notificationAct: bindActionCreators(notificationActions, dispatch),
    authAct: bindActionCreators(authActions, dispatch),
    authChangeLang: bindActionCreators(changeLang, dispatch),
  };
}

function mapStateToProps(state) {
  return {
    process: state.rootReducer.process,
    authState: state.rootReducer.auth,
    location: state.router.location,
    lang: state.rootReducer.auth.lang,
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(injectIntl(Dashboard)));
