import React from 'react';
import { Alert } from 'antd';
import styled from 'styled-components';

const CallOutAlert = styled(Alert)`
  margin: 0 0 0 10px;
  padding: 0px 15px 0px 37px;
  line-height: 30px;

  & {
    .ant-alert-icon {
      top: 8px;
    }
  }
`;

export const CallOut = ({ text }) => {
  if (text && text.user && text.user.hasSubscription) {
    return (
      <CallOutAlert
        message={`${text.user.hasSubscription.Category.name}`.toUpperCase()}
        type="info"
        showIcon
      />
    );
  }
  return '';
};
