import React from 'react';

import { Switch, Route, Redirect } from 'react-router-dom';

import loadable from 'react-loadable';
import LoadingComponent from 'components/AppComponents/Loading';

export const PrivateAdminRoute = loadable({
  loader: () => import('components/PrivateAdminRoute'),
  loading: LoadingComponent,
});
export const DashboardCmp = loadable({
  loader: () => import('components/Dashboard'),
  loading: LoadingComponent,
});
export const ProcessServices = loadable({
  loader: () => import('components/ProcessServices'),
  loading: LoadingComponent,
});
export const CreateProcessFormCmp = loadable({
  loader: () => import('components/ProcessServices/create'),
  loading: LoadingComponent,
});
export const Profile = loadable({
  loader: () => import('components/Profile'),
  loading: LoadingComponent,
});
export const Configuration = loadable({
  loader: () => import('components/Configuration'),
  loading: LoadingComponent,
});
export const Nodes = loadable({
  loader: () => import('components/Nodes'),
  loading: LoadingComponent,
});
export const Models = loadable({
  loader: () => import('components/Models'),
  loading: LoadingComponent,
});
export const Users = loadable({
  loader: () => import('components/Users'),
  loading: LoadingComponent,
});
export const Subscriptions = loadable({
  loader: () => import('components/Subscriptions'),
  loading: LoadingComponent,
});
export const Menus = loadable({
  loader: () => import('components/Menus'),
  loading: LoadingComponent,
});
export const Eds = loadable({
  loader: () => import('components/Eds'),
  loading: LoadingComponent,
});
export const Services = loadable({
  loader: () => import('components/Services'),
  loading: LoadingComponent,
});
export const Engines = loadable({
  loader: () => import('components/Engines'),
  loading: LoadingComponent,
});
export const Stats = loadable({
  loader: () => import('components/Stats'),
  loading: LoadingComponent,
});
export const NotFound = loadable({
  loader: () => import('components/NotFound'),
  loading: LoadingComponent,
});
export const PayCancel = loadable({
  loader: () => import('components/PayCancel'),
  loading: LoadingComponent,
});
export const PayError = loadable({
  loader: () => import('components/PayError'),
  loading: LoadingComponent,
});
export const PaySuccess = loadable({
  loader: () => import('components/PaySuccess'),
  loading: LoadingComponent,
});
export const Actors = loadable({
  loader: () => import('components/Actors'),
  loading: LoadingComponent,
});

export const Projects = loadable({
  loader: () => import('components/Projects'),
  loading: LoadingComponent,
});

export const Jobs = loadable({
  loader: () => import('components/Jobs'),
  loading: LoadingComponent,
});

export const Routes = ({
  authState,
  messages,
  _changeFileID,
  allowedFiles,
  defaultValue,
  _selectProcess,
  setCollapsed,
}) => {
  return (
    <Switch>
      <Route exact path="/dashboard" render={() => <DashboardCmp user={authState.user} />} />
      <Route exact path="/dashboard/users" render={() => <Users userLogin={authState.user} />} />
      <Route
        exact
        path="/dashboard/subscriptions"
        render={() => <Subscriptions messages={messages} userLogin={authState.user} />}
      />
      <Route
        exact
        path="/dashboard/subscriptions/user/:id"
        render={() => <Subscriptions messages={messages} userLogin={authState.user} />}
      />
      <Route
        exact
        path="/dashboard/users/:rol/:id"
        render={() => <DashboardCmp userLogin={authState.user} />}
      />
      <Route
        exact
        path="/dashboard/process-services"
        render={() => (
          <ProcessServices
            userLogin={authState.user}
            typeOfUser={authState.user.typeOfUser}
            changeFileID={_changeFileID}
            process={process}
            allowedFiles={allowedFiles}
            defaultValue={defaultValue}
            selectProcess={_selectProcess}
            newProcess={false}
            setCollapsed={setCollapsed}
          />
        )}
      />
      <Route
        exact
        path="/dashboard/process-services/new"
        render={() => (
          <CreateProcessFormCmp
            userLogin={authState.user}
            typeOfUser={authState.user.typeOfUser}
            allowedFiles={allowedFiles}
            setCollapsed={setCollapsed}
            changeFileID={_changeFileID}
          />
        )}
      />

      <Route
        exact
        path="/dashboard/process-services/:uuid/success"
        render={() => <PaySuccess dashboard />}
      />
      <Route
        exact
        path="/dashboard/process-services/:uuid/cancel"
        render={() => <PayCancel dashboard />}
      />
      <Route
        exact
        path="/dashboard/process-services/:uuid/error"
        render={() => <PayError dashboard />}
      />

      <Route exact path="/dashboard/profile" component={Profile} />

      <Route exact path="/dashboard/stats/:userId" render={() => <Stats user={authState.user} />} />
      <Route exact path="/dashboard/stats" render={() => <Stats user={authState.user} />} />

      <PrivateAdminRoute
        exact
        path="/dashboard/configuration"
        user={authState.user}
        component={Configuration}
      />

      <PrivateAdminRoute
        exact
        path="/dashboard/services"
        user={authState.user}
        component={Services}
      />
      <PrivateAdminRoute exact path="/dashboard/nodes" user={authState.user} component={Nodes} />

      <PrivateAdminRoute
        exact
        path="/dashboard/engines"
        user={authState.user}
        client={true}
        component={Engines}
      />
      <PrivateAdminRoute
        exact
        client={true}
        path="/dashboard/engines/user/:userId"
        user={authState.user}
        component={Engines}
      />
      <PrivateAdminRoute
        exact
        user={authState.user}
        client={true}
        path="/dashboard/models"
        component={Models}
      />
      <PrivateAdminRoute exact user={authState.user} path="/dashboard/actors" component={Actors} />

      <PrivateAdminRoute
        exact
        user={authState.user}
        client={true}
        path="/dashboard/projects"
        component={Projects}
      />
      <PrivateAdminRoute exact user={authState.user} path="/dashboard/jobs" component={Jobs} />
      <PrivateAdminRoute exact path="/dashboard/eds" user={authState.user} component={Eds} />
      <PrivateAdminRoute
        exact
        path="/dashboard/eds/:filter/:id"
        user={authState.user}
        component={Eds}
      />

      <Route exact path="/dashboard/404" render={() => <NotFound dashboard />} />
      <Redirect to="/dashboard/404" />
    </Switch>
  );
};
